
export function setDeCampos(
  formato: string,
  valores: any
): any {
  var str: string = "";
  if (formato) {
    var lineas = formato.split("#");
    for (var linea of lineas) {
      
      if (linea.includes("<")) {
        var clave = linea.replace(/</g, "");
         clave = clave.replace(/>/g, "");
        str = str + valores[clave];
      } else if (linea.includes("'")) {
        str = str + linea.replace(/'/g, "");
      } else {
        str = str + linea;
      }
    }
  }

  return str;
}







