  cargarSetGarantiaHipotecaria() {
    if (!this.setDeCamposService.setGarantiaHipotecariaSeleccionado) {
      return;
    }
        const { 
          descripcion,
          valorEstimado,
          antigedad,
      pais,
      codigoPostal,
      estado,
      ciudad,
      colonia,
      municipio,
      calle,
      numeroExterior,
      numeroInterior,
        } = this.setDeCamposService.setGarantiaHipotecariaSeleccionado;; 
  

  
        this.setGarantiaHipotecariaForm.patchValue({ 
          descripcion,
          valorEstimado,
          antigedad,
      pais,
      codigoPostal,
      estado,
      ciudad,
      colonia,
      municipio,
      calle,
      numeroExterior,
      numeroInterior,
        });
  }

