public catPaiss: CatalogoGeneral[] = [];public catColonia: CatCodigoPostal[] = [];public catEntidadMexicos: CatEntidad[] = [];public catLocalidads: CatLocalidad[] = [];public catMunicipios: CatMunicipio[] = [];cargarCatalogosControl(){  this.cargarCatalogoColeccion("catEntidadMexico").subscribe((catalogo) => {    this.catEntidadMexicos = catalogo;  });  this.cargarCatalogoColeccion("catPais").subscribe((catalogo) => {    this.catPaiss = catalogo;  });}  openModalBusquedaCP() {    this.catalogoGeneralService.CPBusqueda= this.setGarantiaHipotecariaForm.get("codigoPostal").value;    this.modalService      .open(BusquedaCodigoPostalComponent, {        backdropClass: "light-blue-backdrop",        backdrop: false,        animation: true,        size: "xl",      })      .result.then(        (result) => {                    this.setGarantiaHipotecariaForm.get("codigoPostal").setValue(this.catalogoGeneralService.CPSeleccionado.d_codigo);          this.setGarantiaHipotecariaForm.get("estado").setValue(this.catalogoGeneralService.CPSeleccionado.d_estado.toUpperCase());          this.setGarantiaHipotecariaForm.get("municipio").setValue(this.catalogoGeneralService.CPSeleccionado.D_mnpio.toUpperCase());          this.setGarantiaHipotecariaForm.get("colonia").setValue(this.catalogoGeneralService.CPSeleccionado.d_asenta);          this.setGarantiaHipotecariaForm.get("ciudad").setValue(this.catalogoGeneralService.CPSeleccionado.d_ciudad.toUpperCase());        },        (reason) => {          this.setGarantiaHipotecariaForm.get("codigoPostal").setValue(this.catalogoGeneralService.CPSeleccionado.d_codigo);          this.setGarantiaHipotecariaForm.get("estado").setValue(this.catalogoGeneralService.CPSeleccionado.d_estado.toUpperCase());          this.setGarantiaHipotecariaForm.get("municipio").setValue(this.catalogoGeneralService.CPSeleccionado.D_mnpio.toUpperCase());          this.setGarantiaHipotecariaForm.get("colonia").setValue(this.catalogoGeneralService.CPSeleccionado.d_asenta);          this.setGarantiaHipotecariaForm.get("ciudad").setValue(this.catalogoGeneralService.CPSeleccionado.d_ciudad.toUpperCase());        }      );  }//cargarCatalogoColeccion(catalogo: string) {//  return this.catalogoGeneralService.cargarCatalogoColeccion(catalogo);//}tipoCaptura() {  let pais = this.setGarantiaHipotecariaForm.get("pais").value;  if (pais == "Mxico") {    return true;  } else {    return false;  }}cambioEstado(campo: string) {  let estado = this.setGarantiaHipotecariaForm.get("estado").value;  let municipio = this.setGarantiaHipotecariaForm.get("municipio").value;  let param = { claveestado: estado, clavemunicipio: municipio };  switch (campo) {    case "municipio":      this.cargarCatalogoColeccionParam("catLocalidad", param).subscribe(        (catalogo) => {          this.catLocalidads = catalogo;                  }      );      this.cargarCatalogoColeccionParam("catColonia", param).subscribe((catalogo) => {        this.catColonia = catalogo;      });      this.setGarantiaHipotecariaForm.get("ciudad").setValue('');      this.setGarantiaHipotecariaForm.get("colonia").setValue('');      break;    case "estado":      this.cargarCatalogoColeccionParam("catMunicipio", param).subscribe(        (catalogo) => {          this.catMunicipios = catalogo;        }      );      this.setGarantiaHipotecariaForm.get("municipio").setValue('');      this.setGarantiaHipotecariaForm.get("ciudad").setValue('');      this.setGarantiaHipotecariaForm.get("colonia").setValue('');      break;  }}cargarCatalogoColeccionParam(catalogo: string, param: {}) {  return this.catalogoGeneralService.cargarCatalogoColeccionParam(    catalogo,    param  );}





