  isoDate(fecha:Date) { 
    var d=new Date(fecha);
      return d.getUTCFullYear() + '-' + this.pad(d.getUTCMonth() + 1) + 
      '-' + this.pad(d.getUTCDate()) + 'T' + this.pad(d.getHours()) + ':' 
      + this.pad(d.getUTCMinutes()) + ':' + this.pad(d.getUTCSeconds()); 
    }
  
    pad(n:number){return n<10 ? '0'+n : n}


//modal

openModalDependenciaPuestoPeriodo(accion:string) {
  if (accion!='editar'){
    this.setDeCamposService.setDependenciaPuestoPeriodoSeleccionado=undefined;
  }
  this.modalService
    .open(SetDependenciaPuestoPeriodoComponent, {    backdropClass: "light-blue-backdrop",
    backdrop: false,
    animation: true,
    size: "lg", })
    .result.then((result) => {this.anadirDependenciaPuestoPeriodo(result);});
}

//Campo - DependenciaPuestoPeriodo

get dependenciaPuestoPeriodo(): FormArray {
  return this.setDatosGeneralesCompletosPFForm.get("dependenciaPuestoPeriodo") as FormArray;
}

anadirDependenciaPuestoPeriodo(campo: AbstractControl) {
  if (this.setDeCamposService.setDependenciaPuestoPeriodoSeleccionado){
    this.borrarDependenciaPuestoPeriodo(this.dependenciaPuestoPeriodoIdEdicion);
  }
  this.dependenciaPuestoPeriodo.push(campo);
}

editarDependenciaPuestoPeriodo(campo:SetDependenciaPuestoPeriodo,index:number) {
  this.dependenciaPuestoPeriodoIdEdicion=index;
  if (campo){
    this.setDeCamposService.setDependenciaPuestoPeriodoSeleccionado=campo;
    this.openModalDependenciaPuestoPeriodo('editar');
  }
}

borrarDependenciaPuestoPeriodo(indice: number) {
  this.dependenciaPuestoPeriodo.removeAt(indice);
}

setCamposDependenciaPuestoPeriodo(valores: JSON) {
  return setDeCampos(this.dependenciaPuestoPeriodoConcatenacion,valores);
}

















