import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class FuncionesFecha {
  constructor() {}

  validarHora12(hora: string): boolean {
    // Expresin regular para validar el formato de la hora en formato de 12 horas (hh:mm am/pm)
    const formatoHora12 = /^(0?[1-9]|1[0-2]):[0-5][0-9] (am|pm)$/i;

    // Verificar si la hora coincide con el formato hh:mm am/pm


    if (formatoHora12.test(hora)) {
      // Separar la hora, los minutos y am/pm
      const partesHora = hora.split(" ");
      const tiempo = partesHora[1].toUpperCase(); // Convertir a maysculas para ser case-insensitive

      // Obtener horas y minutos
      const [horaParte, minutos] = partesHora[0].split(":");
      const horas = parseInt(horaParte, 10);
      const minutos2 = parseInt(minutos, 10);

      // Verificar si los valores de hora y minutos son vlidos
      if (
        horas >= 1 &&
        horas <= 12 &&
        minutos2>= 0 &&
        minutos2<= 59 &&
        (tiempo === "AM" || tiempo === "PM")
      ) {
        return true; // La hora es vlida
      }
    }

    return false; // La hora no es vlida o no coincide con el formato hh:mm am/pm
  }

  pad(n: number) {
    return n < 10 ? "0" + n : n;
  }

  obtenerFormato12Horas(hora) {
    return hora >= 12 ? "pm" : "am";
  }

  // Funcin para convertir la hora al formato de 12 horas
  convertirFormato12Horas(hora) {
    if (hora === 0) {
      return { hora12: 12, periodo: "am" };
    } else if (hora === 12) {
      return { hora12: 12, periodo: "pm" };
    } else if (hora > 12) {
      if ((hora - 12).toString().length == 1) {
        return { hora12: "0" + (hora - 12), periodo: "pm" };
      } else {
        return { hora12: hora - 12, periodo: "pm" };
      }
    } else {
      if (hora.toString().length == 1) {
        return { hora12: "0" + hora, periodo: "am" };
      } else {
        return { hora12: hora, periodo: "am" };
      }
    }
  }

  convertirHora24a12(hora24: string): string {
    const formatoHora24 = /^([01][0-9]|2[0-3]):([0-5][0-9])$/;

    if (formatoHora24.test(hora24)) {
      const [horaParte, minutos] = hora24.split(":");
      let horas = parseInt(horaParte, 10);

      let tiempo = "am";

      if (horas >= 12) {
        tiempo = "pm";
        if (horas > 12) {
          horas -= 12;
        }
      }

      if (horas === 0) {
        horas = 12; // Si son las 00:00 en formato de 24 horas, se muestra como 12:00 am en formato de 12 horas
      }

      // Formatear la hora en formato de 12 horas
      const hora12 = `${horas
        .toString()
        .padStart(2, "0")}:${minutos} ${tiempo}`;

      return hora12;
    }

    return "Formato de hora incorrecto";
  }

  convertirHora12a24(hora12: string): string {
    const formatoHora12 = /^(0?[1-9]|1[0-2]):([0-5][0-9]) (am|pm)$/i;

    if (formatoHora12.test(hora12)) {
  
      const partesHora = hora12.split(" ");
      const tiempo = partesHora[1].toUpperCase();

      const [horaParte, minutos] = partesHora[0].split(":");
      let horas = parseInt(horaParte, 10);

      if (tiempo === "PM" && horas !== 12) {
        horas += 12;
      } else if (tiempo === "AM" && horas === 12) {
        horas = 0;
      }

      // Formatear la hora en formato de 24 horas (agregando ceros si es necesario)
      const hora24 = `${horas.toString().padStart(2, "0")}:${minutos}`;

      return hora24;
    }

    return "Formato de hora incorrecto";
  }

  // Funcin para rellenar el select con horas en formato de 12 horas
  llenarHoras15Min() {
    let horas: any = [];
    // Crear opciones de horas (de 0 a 23)
    for (var i = 0; i < 24; i++) {
      var hora = this.convertirFormato12Horas(i);
      var hr24 = "";

      if (i.toString().length == 1) {
        hr24 = "0" + i.toString();
      } else {
        hr24 = i.toString();
      }

      var valor = ""; // Aadir la hora al valor de la opcin en formato de 24 horas
      for (var j = 0; j <= 3; j++) {
        let valor2 = "";
        switch (j) {
          case 0:
            valor2 = hora.hora12 + ":00 " + hora.periodo;
            valor = hr24 + ":00";
            break;
          case 1:
            valor2 = hora.hora12 + ":15 " + hora.periodo;
            valor = hr24 + ":15";
            break;
          case 2:
            valor2 = hora.hora12 + ":30 " + hora.periodo;
            valor = hr24 + ":30";
            break;
          case 3:
            valor2 = hora.hora12 + ":45 " + hora.periodo;
            valor = hr24 + ":45";
            break;
        }

        // Mostrar la hora en el texto de la opcin en formato de 12 horas
        horas.push({ hora24: valor, hora12: valor2 });
      }
    }
    return horas;
  }

  isoDate(fecha: Date) {
    var d = new Date(fecha);
    return (
      d.getUTCFullYear() +
      "-" +
      this.pad(d.getUTCMonth() + 1) +
      "-" +
      this.pad(d.getUTCDate()) +
      "T" +
      this.pad(d.getHours()) +
      ":" +
      this.pad(d.getUTCMinutes()) +
      ":" +
      this.pad(d.getUTCSeconds())
    );
  }

  isoDateHoras(fecha: Date) {
    var d = new Date(fecha);
    return this.pad(d.getHours()) + ":" + this.pad(d.getUTCMinutes());
  }

  isoDateFecha(fecha: Date) {
    var d = new Date(fecha);


    return (
      d.getUTCFullYear() +
      "-" +
      this.pad(d.getUTCMonth() + 1) +
      "-" +
      this.pad(d.getUTCDate())
    );
  }
  isoFechaActual() {
    var fechaActual: Date = new Date();
    var d = new Date(fechaActual);


    return (
      d.getUTCFullYear() +
      "-" +
      this.pad(d.getUTCMonth() + 1) +
      "-" +
      this.pad(d.getUTCDate())
    );
  }


  calcularEdad(fechaNacimiento: string): any {

    var hoy = new Date();
    var cumpleanos = new Date(fechaNacimiento);

    var hoyDate = hoy.getDate()
    var hoyMes = hoy.getMonth()
    var hoyAnio = hoy.getFullYear()
    var cumpleDate = cumpleanos.getDate()
    var cumpleMes = cumpleanos.getMonth()
    var cumpleAnio = cumpleanos.getFullYear()

    var edadString = "";


    //get years  
    var anioEdad = hoyAnio - cumpleAnio;

    //get months  
    if (hoyMes >= cumpleMes)
      //get months when current month is greater  
      var mesEdad = hoyMes - cumpleMes;
    else {
      anioEdad--;
      var mesEdad = 12 + hoyMes - cumpleMes;
    }

    //get days  
    if (hoyDate >= cumpleDate)
      //get days when the current date is greater  
      var diasEdad = hoyDate - cumpleDate;
    else {
      mesEdad--;
      var diasEdad = 31 + hoyDate - cumpleDate;

      if (mesEdad < 0) {
        mesEdad = 11;
        anioEdad--;
      }
    }

    if (anioEdad > 0)
      edadString = anioEdad + " aos " + mesEdad + " meses " + diasEdad + " dias";
    else if ((anioEdad == 0) && (mesEdad > 0))
      edadString = mesEdad + " meses " + diasEdad + " dias";
    else if ((anioEdad == 0) && (mesEdad == 0))
      edadString = diasEdad + " dias";
    
    return edadString;
  }

}
