import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthGuard } from "../guards/auth.guard";
import { PagesComponent } from "./pages.component";
import { NotfoundComponent } from "../authentication/404/not-found.component";

const routes: Routes = [
  {
    path: "pages",
    component: PagesComponent,
    canActivate: [AuthGuard],
    canLoad: [AuthGuard],
    loadChildren: () =>
      import("./child-routes.module").then((m) => m.ChildRoutesModule),
  },
  { path: "", redirectTo: "/login", pathMatch: "full" },
  // {
  //   path: 'paciente',
  //   component: PacientewebLayoutComponent,
  //     //   canActivate: [PacienteGuard],
  // //   canLoad: [PacienteGuard],
  //   children: [
  //       {
  //         path: '',
  //         loadChildren: () =>   import("./pacienteweb/paciente.module").then((m) => m.PacienteModule),
  //       }
  // ]},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {}
