  async actualizarToken() {
    const expira = Number(localStorage.getItem("expires_at"));
    const expiraDate = new Date();
    expiraDate.setTime(expira);
    // console.log(expira);
    // console.log(expiraDate);
    // console.log(new Date());

    if (expiraDate < new Date()) {
      await this.auth2.signIn().then((user) => {
        const resp = user.getAuthResponse();
        localStorage.setItem("access_token", resp.access_token);
        localStorage.setItem("expires_at", resp.expires_at);
      });
    }
  }

  guardarLocalStorage(token: string, menu: any) {
    localStorage.setItem("token", token);
    localStorage.setItem("menu", JSON.stringify(menu));
  }

  logout() {
    localStorage.removeItem("token");
    localStorage.removeItem("access_token");
    localStorage.removeItem("menu");
    this.auth2.signOut().then(() => {
      this.ngZone.run(() => {
        this.router.navigateByUrl("/login");
      });
    });
  }

  validarToken(): Observable<boolean> {
    return this.http
      .get(`${base_url}/login/renew`, {
        headers: {
          "x-token": this.token,
        },
      })
      .pipe(
        map((resp: any) => {
          this.guardarLocalStorage(resp.token, resp.menu);

          return true;
        }),
        catchError((error) => of(false))
      );
  }

