import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';
//<%CampolineaSetCampos%>import { <%PLMa-claveCampo%> } from '../interfaces/<%PLMi-claveCampo%>.interface';
import { CatalogoGeneral } from '../../models/controles/catalogoGeneral.model';
import { CatCodigoPostal } from 'src/app/models/controles/catCodigoPostal.model';
import { ColUsoCfdiSatAldaDev } from 'src/app/models/controles/colUsoCfdiSatAldaDev.model';
import { ColRegimenFiscalAldaDev } from 'src/app/models/controles/colRegimenFiscalAldaDev.model';

const base_url = environment.base_url;


@Injectable({
  providedIn: 'root'
})
export class CatalogoGeneralService {

  public CPBusqueda:string;
  public CPSeleccionado:CatCodigoPostal;


  
  public ClaveProvBusqueda:string;

  
  public ClaveUsoCFDI:string;
  public ClaveUsoCFDISeleccionado:ColUsoCfdiSatAldaDev;

  public ClaveRFBusqueda:string;
  public ClaveRFSeleccionado:ColRegimenFiscalAldaDev;


  public catalogoGeneralId:string='';

  constructor( private http: HttpClient ) { }

  cargarCatalogoGenerals(claveCatalogo:string) {
    const url = `${ base_url }/catalogoGeneral`;
    return this.http.get(url,{ 
        params: {'claveCatalogo': claveCatalogo,}})
              .pipe(
                map((
                  resp: {ok: boolean, catalogoGenerals: CatalogoGeneral[] }
                  ) => resp.catalogoGenerals 
                  )
              );
  }

  cargarCatalogoColeccion(catalogo:string) {
    const url = `${ base_url }/catalogoColeccion/${catalogo}`;
    return this.http.get(url)
              .pipe(
                map((
                  resp: {ok: boolean, catalogo: any[] }
                  ) => resp.catalogo 
                  )
              );
  }

  cargarCatalogoColeccionCliente(catalogo:string) {
    const url = `${ base_url }/catalogoColeccioncliente/${catalogo}`;
    return this.http.get(url)
              .pipe(
                map((
                  resp: {ok: boolean, catalogo: any[] }
                  ) => resp.catalogo 
                  )
              );
  }
  
  cargarCatalogoColeccionParam(catalogo:string,param:{}) {

    const url = `${ base_url }/catalogoColeccion/${catalogo}`;
    return this.http.get(url,{ 
      params:param})
              .pipe(
                map((
                  resp: {ok: boolean, catalogo: any[] }
                  ) => resp.catalogo 
                  )
              );
  }


  cargarCatalogoColeccionTermino(catalogo:string,termino:string) {

 
    const url = `${ base_url }/catalogoColeccion/${catalogo}/${termino}`;
    return this.http.get(url)
              .pipe(
                map((
                  resp: {ok: boolean, catalogo: any[] }
                  ) => resp.catalogo 
                  )
              );
  }

  cargarCatalogoColeccionPorId(catalogo:string,id:string) {
    const url = `${ base_url }/catalogoColeccion/id/${catalogo}/${id}`;

    return this.http.get(url)
              .pipe(
                map((
                  resp: {ok: boolean, catalogo: any[] }
                  ) => {
                    console.log('entra2')
                    resp.catalogo }
                  )
              );
  }



  
 }

