import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';
//<%CampolineaSetCampos%>import { <%PLMa-claveCampo%> } from '../interfaces/<%PLMi-claveCampo%>.interface';
import { CatalogoGeneralCliente } from 'src/app/models/controles/catalogoGeneralCliente.model';

const base_url = environment.base_url;


@Injectable({
  providedIn: 'root'
})
export class CatalogoGeneralClienteService {

  public catalogoGeneralId:string='';
  constructor( private http: HttpClient ) { }

  get token(): string {
    return localStorage.getItem('token') || '';
  }

  get headers() {
    return {
        headers: {
          "x-token": this.token,
        },
        params: {
          idrelacion:  '',
          tiporelacion: "registroMiEmpresa",
        }
    }
  }


  
  cargarCatalogoGenerals(claveCatalogo:string) {

     const url = `${ base_url }/catalogoClienteGeneral/catalogosGenerales/${ claveCatalogo }`;
  
    return this.http.get( url,this.headers)
              .pipe(
                map( (resp: {ok: boolean, catalogoGenerals: CatalogoGeneralCliente[] }) => 
                resp.catalogoGenerals )
              );

   }

   
 cargarCatalogoGeneralsColeccion(claveCatalogo:string) {

  const url = `${ base_url }/catalogoClienteGeneral/colecion/${ claveCatalogo }`;

 return this.http.get( url,this.headers)
           .pipe(
             map( (resp: {ok: boolean, catalogo: any[] }) => 
             resp.catalogo )
           );

}
  
 }
   




