  openModalTelefonosContacto(accion: string) {
    if (accion != "editar") {
      this.setDeCamposService.setTelefonoContactoSeleccionado = undefined;
    }
    this.modalService
      .open(SetTelefonoContactoComponent, {
        backdropClass: "light-blue-backdrop",
        backdrop: false,
        animation: true,
        size: "lg",
      })
      .result.then((result) => {
        this.anadirTelefonosContacto(result);
      });
  }

