import { Component, OnInit, OnDestroy, ElementRef } from "@angular/core";

import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import Swal from "sweetalert2";
import { RegistroUsuarioService } from "src/app/services/controles/registroUsuario.service";

import { Router } from "@angular/router";
import { RegistroMiEmpresaService } from "src/app/services/controles/registroMiEmpresa.service";
import { RegistroUsuarioComponent } from "src/app/components/registroUsuario/registroUsuario.component";
import {
  NgbActiveModal,
  NgbModal,
  NgbModalRef,
} from "@ng-bootstrap/ng-bootstrap";

// declare var $:any;

@Component({
  selector: "login-cmp",
  templateUrl: "./login.component.html",
  styleUrls: ["login.component.scss"],
})
export class LoginComponent implements OnInit, OnDestroy {
  focus;
  focus1;
  public loading: boolean = false;
  test: Date = new Date();
  public formSubmitted = false;
  public loginForm: FormGroup;

  constructor(
    private element: ElementRef,
    private router: Router,
    private fb: FormBuilder,
    private registroUsuarioService: RegistroUsuarioService,
    private registroMiEmpresaService: RegistroMiEmpresaService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal
  ) {}

  ngOnInit() {
    this.loginForm = this.fb.group({
      email: [
        localStorage.getItem("email") || "",
        [Validators.required, Validators.email],
      ],
      password: ["", Validators.required],
      remember: [false],
    });

    var body = document.getElementsByTagName("body")[0];
    body.classList.add("login-page");
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName("body")[0];
    body.classList.remove("login-page");
  }

  //ALDA

  cambioPassword() {
    var { email } = this.loginForm.value;

    if (email) {
      // this.correoService
      //   .cambioPassword(this.loginForm.value)
      //   .subscribe((resp: any) => {
      //     //${  }
      //     Swal.fire(
      //       "Se envi correo para restablecer contrasea revise su correo",
      //       email,
      //       "success"
      //     );
      //     // this.router.navigateByUrl(`/login`)
      //   });
    } else {
      Swal.fire("Es necesario ingresar su correo");
    }
  }

  login() {
    this.registroUsuarioService.login(this.loginForm.value).subscribe(
      (resp) => {
        if (this.loginForm.get("remember").value) {
          localStorage.setItem("email", this.loginForm.get("email").value);
        } else {
          localStorage.removeItem("email");
        }

        localStorage.setItem("registroUsuarioId", resp.usuario._id);
        localStorage.setItem("nombreCompleto", resp.usuario.nombreCompleto);
        localStorage.setItem("usuarioRol", resp.usuario.rolUsuario);

        let usuarioAsignadoRol = resp.usuario.rolUsuario;

        localStorage.setItem(
          "nombreDeUsuario",
          resp.usuario.nombre + " " + resp.usuario.apellidoPaterno
        );
        localStorage.setItem("empresa", resp.usuario.registroMiEmpresa._id);
        this.registroUsuarioService.registroUsuarioSeleccionado = resp.usuario;
        this.registroMiEmpresaService.registroMiEmpresaSeleccionado =
          resp.usuario.registroMiEmpresa;

        if (usuarioAsignadoRol == "Administrador") {
          this.router.navigateByUrl("/pages/principalunisofom/principalformulario");
          localStorage.setItem(
            "rutadashboard",
            "/pages/principalunisofom/principalformulario"
          );
        } else {
          this.router.navigateByUrl("/pages/principalunisofom/principalusuario");
          localStorage.setItem("rutadashboard", "/pages/principalunisofom/principalusuario");
        }
      },
      (err) => {
        // Si sucede un error
        console.log(err);
        alert("Error encontrado");
        // Swal.fire("Error", err.error.msg, "error");
      }
    );
  }

  RegistroUsuario(): void {
    this.AbrirModal(RegistroUsuarioComponent, undefined, undefined);
  }

  AbrirModal(componente: any, row, relacion) {
    const modalRef: NgbModalRef = this.modalService.open(componente, {
      backdropClass: "light-blue-backdrop",
      backdrop: false,
      animation: true,
      size: "xl",
    });

    modalRef.componentInstance.registroNuevoFormulario = row;
    // modalRef.componentInstance.registroNuevoFormularioId = row?row._id:undefined;
    modalRef.componentInstance.Id = row ? row._id : undefined;
    modalRef.result.then(
      (result) => {},
      (reason) => {}
    );
  }
}
