import {
  Component,
  OnInit,
  OnDestroy,
  ChangeDetectionStrategy,
  ViewChild,
  TemplateRef,
} from "@angular/core";
import { Subscription } from "rxjs";
import { delay, map } from "rxjs/operators";
import Swal from "sweetalert2";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import {
  ModalDismissReasons,
  NgbActiveModal,
  NgbModal,
} from "@ng-bootstrap/ng-bootstrap";
import { CatalogoGeneralService } from "src/app/services/controles/catalogoGeneral.service";

import { MatTableDataSource } from "@angular/material/table";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";

export enum SelectionType {
  single = "single",
  multi = "multi",
  multiClick = "multiClick",
  cell = "cell",
  checkbox = "checkbox",
}

//Documentos

@Component({
  selector: "app-busquedaCodigoPostal",
  templateUrl: "./busquedaCodigoPostal.component.html",
  styleUrls: ["./busquedaCodigoPostal.component.scss"],
})
export class BusquedaCodigoPostalComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  public cargando: boolean = true;
  // public catalogosCodigoPostal: CatalogoCodigoPostal[] = [];
  query: string = "";

  //**modal */
  //**modal */
  entries: number = 10;
  selected: any[] = [];
  temp = [];
  activeRow: any;
  dataSource: any = [];

  public closeResult = "";
  public formularioRegistro = "modal";

  constructor(
    private catalogoGeneralService: CatalogoGeneralService,
    public activeModal: NgbActiveModal
  ) {}
  ngOnInit(): void {
    // this.cargarDatos();
  }

  cargarDatos() {


    this.cargarCatalogoColeccion("catCodigoPostal", this.query).subscribe(
      (catalogo) => {
        // this.catalogosCodigoPostal = catalogo;
        this.dataSource = catalogo;
        this.temp = this.dataSource.map((prop, key) => {
          return {
            ...prop,
            id: key,
          };
        });

        console.log(catalogo)
      }
    );
  }
  entriesChange($event) {
    this.entries = $event.target.value;
  }

 filterTable($event) {
    const val = $event.target.value.toLowerCase();

    this.query=val;
    if (!val) {
      this.temp = [...this.dataSource];
      return;
    }

    this.temp = this.dataSource.filter((row) => {
      return Object.values(row).some((campo) =>
        campo?.toString().toLowerCase().includes(val)
      );
    });
  }


  onSelect({ selected }) {
    this.selected.splice(0, this.selected.length);
    this.selected.push(...selected);
  }
  onActivate(event) {
    this.activeRow = event.row;
  }

  cargarCatalogoColeccion(catalogo: string, termino: string) {
    this.cargando = false;
    if (termino == "") {
  
      return this.catalogoGeneralService.cargarCatalogoColeccion(catalogo);
    } else {
     
      return this.catalogoGeneralService.cargarCatalogoColeccionTermino(
        catalogo,
        termino
      );
    }
  }

  seleccionar(row) {
    //console.log(row)

    console.log(row)
    this.catalogoGeneralService.CPSeleccionado = row;
    // this.activeModal.dismiss();
    // this.modalService.dismissAll()
    this.activeModal.close(BusquedaCodigoPostalComponent);
  }
}
