  isoDate(fecha:Date) { 
    var d=new Date(fecha);
      return d.getUTCFullYear() + '-' + this.pad(d.getUTCMonth() + 1) + 
      '-' + this.pad(d.getUTCDate()) + 'T' + this.pad(d.getHours()) + ':' 
      + this.pad(d.getUTCMinutes()) + ':' + this.pad(d.getUTCSeconds()); 
    }
  
    pad(n:number){return n<10 ? '0'+n : n}


//modal

openModalDirecciones(accion:string) {
  if (accion!='editar'){
    this.setDeCamposService.setDireccionSeleccionado=undefined;
  }
  this.modalService
    .open(SetDireccionComponent, {    backdropClass: "light-blue-backdrop",
    backdrop: false,
    animation: true,
    size: "lg", })
    .result.then((result) => {this.anadirDirecciones(result);});
}

//Campo - Direcciones

get direcciones(): FormArray {
  return this.setDatosBasicosVariasDireccionesPMForm.get("direcciones") as FormArray;
}

anadirDirecciones(campo: AbstractControl) {
  if (this.setDeCamposService.setDireccionSeleccionado){
    this.borrarDirecciones(this.direccionesIdEdicion);
  }
  this.direcciones.push(campo);
}

editarDirecciones(campo:SetDireccion,index:number) {
  this.direccionesIdEdicion=index;
  if (campo){
    this.setDeCamposService.setDireccionSeleccionado=campo;
    this.openModalDirecciones('editar');
  }
}

borrarDirecciones(indice: number) {
  this.direcciones.removeAt(indice);
}

setCamposDirecciones(valores: JSON) {
  return setDeCampos(this.direccionesConcatenacion,valores);
}






