  isoDate(fecha:Date) { 
    var d=new Date(fecha);
      return d.getUTCFullYear() + '-' + this.pad(d.getUTCMonth() + 1) + 
      '-' + this.pad(d.getUTCDate()) + 'T' + this.pad(d.getHours()) + ':' 
      + this.pad(d.getUTCMinutes()) + ':' + this.pad(d.getUTCSeconds()); 
    }
  
    pad(n:number){return n<10 ? '0'+n : n}


