  obtenerRegistroUsuarioPorId(id: string) {
    const url = `${base_url}/registroUsuario/${id}`;
    return this.http
      .get(url, this.headers)
      .pipe(
        map(
          (resp: { ok: boolean, registroUsuario: RegistroUsuario }) =>
            resp
        )
      );
  }


  
  crearRegistroUsuario(registroUsuario: {

    nombreDeUsuario: string;
    email: string;
    paisConLada: string;
    telefonoUsuario: string;
    tipoDeUsuario: string;
    subTipoDeUsuario: string;
    rolUsuario: string;

  }) {
    const url = `${base_url}/registroUsuario`;
    return this.http.post(url, registroUsuario, this.headers);
  }

  actualizarRegistroUsuario(
    _id: string,
    registroUsuario: {

      nombreDeUsuario: string;
      email: string;
      paisConLada: string;
      telefonoUsuario: string;
      tipoDeUsuario: string;
      subTipoDeUsuario: string;
      rolUsuario: string;

    }
  ) {
    const url = `${base_url}/registroUsuario/${_id}`;
    return this.http.put(url, registroUsuario, this.headers);
  }

  borrarRegistroUsuario(_id: string) {
    const url = `${base_url}/registroUsuario/${_id}`;
    return this.http.delete(url, this.headers);
  }

  //**********************inicio de sesion */

  get imagenUrl() {
    // console.log(`${ base_url }/upload/usuarios/no-image` )
    if (!this.registroUsuario.img) {
      return `${base_url}/upload/registroUsuario/no-image`;
    } else if (this.registroUsuario.img.includes("https")) {
      return this.registroUsuario.img;
    } else if (this.registroUsuario.img) {
      return `${base_url}/upload/registroUsuario/${this.registroUsuario.img}`;
    } else {
      return `${base_url}/upload/registroUsuario/no-image`;
    }
  }

