  guardarLocalStorage(token: string, menu: any, usuario: any) {
    localStorage.setItem("token", token);
    localStorage.setItem("usuario", usuario);
    localStorage.setItem("menu", JSON.stringify(menu));
  }

  logout() {
    localStorage.removeItem("token");
    localStorage.removeItem("access_token");
    localStorage.removeItem("menu");

    if (this.auth2) {
      this.auth2
        .signOut()
        .then(() => {
          this.ngZone.run(() => {
            this.router.navigateByUrl("/");
          });
        })
        .catch((e) => {
          this.router.navigateByUrl("/");
        });
    } else {
      this.router.navigateByUrl("/");
    }
    location.reload();
  }

  validarToken(): Observable<any> {
    if (!this.token) {
      return of(false);
    }

    return this.http
      .get(`${base_url}/login/renew`, {
        headers: {
          "x-token": this.token,
        },
      })
      .pipe(
        map((resp: any) => {
          //  const { email, google, nombres, rolUsuario, img = '', _id } = resp.usuario;
          this.registroUsuario = resp.usuario;
          if (this.registroUsuario.emailConfirmado) {
            return true;
          }
          this.guardarLocalStorage(resp.token, resp.menu, resp.usuario._id);

          Swal.fire({
            icon: "warning",
            title: "Usuario an no validado",
            showDenyButton: true,
            denyButtonText: `Reenviar correo`,
            denyButtonColor: "#3085d6",
            confirmButtonText: "Cerrar",
            confirmButtonColor: "#3085d6",

            text: `Al momento de su registro le enviamos un correo para su validacin.
            Por favor haga clic en el vnculo del correo para validar este usuario.`,
          }).then((result) => {
            if (result.isDenied) {
              Swal.fire("Se envi correo de validacin", "", "info");
              const url = `${base_url}/registroUsuarEEe`;
              console.log(this.http.post(url, this.headers));
            }
            return false;
          });

          //  this.usuario = new RegistroUsuario( nombres, email, '', img, google, rolUsuario, _id );
        }),
        catchError((error) => of(false))
      );
  }

  login(formData: LoginForm) {
    return this.http.post(`${base_url}/login`, formData).pipe(
      tap((resp: any) => {
        this.guardarLocalStorage(resp.token, resp.menu, resp.usuario._id);
      })
    );
  }

  loginGoogle(token) {
    return this.http.post(`${base_url}/login/google`, { token }).pipe(
      tap((resp: any) => {
        this.registroUsuario = resp.usuario;
        this.guardarLocalStorage(resp.token, resp.menu, resp.usuario._id);
      })
    );
  }

  appendPre(message) {
    var pre = document.getElementById("content");
    var textContent = document.createTextNode(message + "\n");
    pre.appendChild(textContent);
  }

  obtenerUsuarioPorToken(id: string) {
    const url = `${base_url}/registroUsuarioPass/${id}`;
    return this.http
      .get(url)
      .pipe(
        map(
          (resp: { ok: boolean; registroUsuario: RegistroUsuario }) =>
            resp.registroUsuario
        )
      );
  }

  actualizarPerfil(data: {
    email: string;
    nombreCompleto: string;
    rolUsuario: string;
  }) {
    data = {
      ...data,
      rolUsuario: this.registroUsuario.rolUsuario,
    };

    return this.http.put(
      `${base_url}/usuarios/${this._id}`,
      data,
      this.headers
    );
  }

  cambioPassword(data: { password: string; password2: string }, token: string) {
    let headers = {
      headers: {
        "x-token": token,
        access_token: this.access_token,
      },
    };

    const url = `${base_url}/registroUsuarioPass`;
    return this.http.post(url, data, headers);
  }

  ValidadEmailUsuario(id: string) {
    const url = `${base_url}/registroUsuarioPass/validaremail/${id}`;
    return this.http
      .get(url)
      .pipe(
        map(
          (resp: { ok: boolean; registroUsuario: RegistroUsuario }) =>
            resp.registroUsuario
        )
      );
  }
